import React, {useState} from 'react';
import { Link, useHistory } from 'react-router-dom';
import axios from 'axios';
const Register = () =>{
    const history = useHistory();
    const [firstName,setFirstName] = useState("");
    const [lastName,setLastName] = useState("");
    const [email,setEmail] = useState("");
    const [password,setPassword] = useState("");

    const setFirstNameHandler = (first) =>{
        setFirstName(first);
    }

    const setLastNameHandler = (last) =>{
        setLastName(last);
    }

    const setEmailHandler = (em) =>{
        setEmail(em);
    }

    const setPasswordHandler = (pass) =>{
        setPassword(pass);
    }

    const registerUser = async () =>{
       await axios.post("https://prototypepushserver.herokuapp.com/api/register",
        {
            first_name:firstName,
            last_name:lastName,
            email:email,
            password:password
        }).then((resp)=>{
            console.log("Successfully registered!");
        })
        .catch((resp)=>{
            console.log("Failed to register: " + resp);
        })
    }

    return(
        <div className="outer">
            <div className="inner">
                <form>
                    <h3>Register</h3>

                    <div className="form-group">
                        <label>First name</label>
                        <input type="text" className="form-control" placeholder="First name" 
                            onChange={first => setFirstNameHandler(first.target.value)} 
                            value={firstName}/>
                    </div>

                    <div className="form-group">
                        <label>Last name</label>
                        <input type="text" className="form-control" placeholder="Last name" 
                        onChange={last => setLastNameHandler(last.target.value)} 
                        value={lastName}/>
                    </div>

                    <div className="form-group">
                        <label>Email</label>
                        <input type="email" className="form-control" placeholder="Enter email" 
                        onChange={em => setEmailHandler(em.target.value)} 
                        value={email}/>
                    </div>

                    <div className="form-group">
                        <label>Password</label>
                        <input type="password" className="form-control" placeholder="Enter password" 
                        onChange={pass => setPasswordHandler(pass.target.value)} 
                        value={password}/>
                    </div>

                    <button type="submit" className="btn btn-dark btn-lg btn-block" onClick={registerUser}>Register</button>
                    <p className="forgot-password text-right">
                        Already registered <Link to="/">log in?</Link>
                    </p>
                </form>
            </div>
        </div>
    )
    
}

export default Register;